	// 批量预授信列表查询
	queryBatchCreditList() {
		this.batchCreditBean.acDateBegin = this.commfunc.transDateN(this.batchCreditBean.acDateBegin);
		this.batchCreditBean.acDateEnd = this.commfunc.transDateN(this.batchCreditBean.acDateEnd);
		this.batchCreditBean.operType = "2";
		this.batchCreditBean.groupId = this.groupId;
		this.httpService.queryBatchPreCreditDetailList(this.batchCreditBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.tableList = data.resultList;
				this.total = data.total;
				this.tableList.forEach(element => {
					element.loanOfficerAdvises = this.transMony(element.loanOfficerAdvises * 10000);
					element.loanSystemAdvises = this.transMony(element.loanSystemAdvises * 10000);
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}
	// 金额格式转换
	transMony(param) {
		let result = "";
		if (param) {
			result = this.commfunc.transform(param) + '';
		}
		result = this.decimalPipe.transform(param, '1.2-2');//申请金额
		return result;
	}
	// 分页
	paginate(event) {

		this.batchCreditBean.pageSize = event.rows * 1;
		let currentPage = event.page + 1;
		this.batchCreditBean.pageNum = currentPage;
		this.first = event.page * this.batchCreditBean.pageSize;
		this.queryBatchCreditList();
	}


	//页面信息查询
	queryViewRelations(resolve, notApply) {
		let post_name = JSON.parse(this.commfunc.getSessionDataCH('postIdList'));
		const params = {
			businessType: "17",
			roleId: post_name,
			viewIndex: 1,
			viewSize: 100,
		};
		console.log(params)
		this.creditCcmService.queryViewRelations(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.list.length > 0) {
					let viewId = [];
					data.list.forEach(e => {
						// if (e.viewName.match('卡要素')) {
						viewId.push({ a: e.viewConfigId, b: e.viewName });
						// }
					});
					this.commfunc.setSessionDataCH('viewId', JSON.stringify(viewId));
					this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify({ businessType: "17", notApply: notApply })]);
				} else {
					this.msgs = [{ severity: 'info', summary: '错误', detail: '岗位未关联页面,请配置关联页面' }];
				}
				// let param = ""

			} else {
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}

	// 个人转授信
	toTranslate(data) {
		let params = {
			batchId: data.batchId,
			serialNo: data.serialNo
		}
		this.httpService.batchPreCreditSubmissionLetter(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.reset();
				// this.getBasic();
				this.queryBatchCreditList();
				this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
			} else {
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
