// 客户群查询
export class SelectCustGroupBean {
    // 入参
    searchScope: string = '';//查询范围
    groupId: string = '';//群号
    groupName: string = '';//群名称
    groupType: string = '';//群类型
    address: string = '';//群地址
    ownerId: string = '';//分组所有者ID
    orgId: string = '';//所属机构
    groupStatus: string = '';//群组状态
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//每页条数
    // 出参新增
    totalNum: number;//总条数
    groupList: Array<groupList>=[];//群列表    
}
export class groupList {
    groupId: string = '';//群号
    groupType: string = '';//群类型
    groupName: string = '';//群名称
    address: string = '';//群地址
    ownerId: string = '';//群主
    ownerName: string = '';//分组所有者名称
    orgId: string = '';//所属机构
    contactList:  Array<contactList>=[];//群联系人列表
    contactListArr:any=[];
    introducerList:  Array<introducerList>=[];//群介绍人列表
    introducerListArr:any=[];
    mentorList:  Array<mentorList>=[];//群指导师傅列表
    mentorListArr:any=[];
    bgcFlag: string = '';//后台中心标志
    description: string = '';//备注（群描述）
    groupStatus: string = '';//群组状态
}
export class contactList {
    adminType: string = '';//管理员类型
    roleId: string = '';//角色ID
    roleName: string = '';//角色姓名
    groupZone: string = '';//客群区域  
    
}
export class introducerList {
    adminType: string = '';//管理员类型
    roleId: string = '';//角色ID
    roleName: string = '';//角色姓名  
}
export class mentorList {
    adminType: string = '';//管理员类型
    roleId: string = '';//角色ID
    roleName: string = '';//角色姓名  
}