export class ApprovedItemsBean {
    processCode: string;
    systemCode: string;
    userId: string;
    startUserId: string;
    noStartFlag: string;
    endTime: string;
    pageRows: number; // 每页记录数
    curPage: number; // 当前页数
    startRows: number; // 开始记录数
    totalRows: number; // 总记录数
    partyOrGroupNo: string; // 事项类型
    bigClassNo: string;
    bigClassName: string; // 事项名称
    littleClassName: string; // 客户号/群号
    partyOrGroupName: string; // 客户姓名/群名
    itemType: string;
    itemName: string;
    workCreateName: string;//发起人
    startUserIdName: string;//发起人
    bussinessWhere: string;
    nodeType: string;

}
export class BatchCreditBean {
    groupId: string;//客户群号
    groupName: string;//客户群名称
    groupType: string;//客户群类型
    custNo: string;//客户号
    custName: string;//客户名称
    operId: string;//经办人
    operName: string;//经办人名称
    bankId: string;//机构码
    bankName: string;//机构名称
    state: string;//状态
    acDateBegin: Date = null;//经办日期(开始)
    acDateEnd: Date = null;//经办日期(结束)
    operType: string;//操作类型
    // groupId: string;//客户群号
    pageNum: number;//当前页
    pageSize: number;//每页记录数
}

