import { Component, OnInit } from "@angular/core";
import { Message } from "primeng/primeng";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ActivatedRoute } from '@angular/router';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { FormGroup, FormBuilder } from "@angular/forms";
// import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'my-element-org',
    templateUrl: './my-element-org.component.html',
    styleUrls: ['./my-element-org.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class MyElementOrg implements OnInit {
    userform: FormGroup;
    detailList: any = [];//基本数据
    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    handleDate: string;
    code: any;
    msgs: Message[];
    nameList: any = [];
    noPromptDispaly: boolean = false;
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    tablelist: any = [];
    taskRate: string;
    canUp: boolean = false;
    flag: string;
    isHistory: boolean = false;
    integralInfo: any;

    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    beanExam: any;
    adjustmentOrgList: any = [];//机构集合
    operationType: any;//手工录入
    operationTypeList: any;//手工录入
    constructor(
        public commfunc: Commfunc,
        public routeInfo: ActivatedRoute,
        public httpService: CusOperationHttpService,
        // private commonFunc: Customtools,
        public fb: FormBuilder,
    ) {
        // 获取参数
        this.detailList = this.routeInfo.snapshot.params;
        this.beanExam = JSON.parse(this.routeInfo.snapshot.params.dataMap);
        // this.bean = JSON.parse(this.routeInfo.snapshot.params.dataMap)
        this.taskRate = this.routeInfo.snapshot.params['taskRate'];
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        if (this.taskRate == '1' && !this.isHistory) {
            this.canUp = true;
        } else {
            this.canUp = false;
        }
        this.userform = fb.group({
            partyClassificationGroupId: [''],//群号
            orgName: [''],//所属机构
            orgId: [''],//所属机构
            groupName: [''],//群名称
            ownerName: [''],//群主
            ownerId: [''],//群主
            afterOrgName: [''],//所属机构
            afterOrgId: [''],//所属机构
        })
    }
    ngOnInit() {
        this.codeValues();
        this.operationType = this.beanExam.operationType;
        this.operationTypeList = this.code['operationTypeGroup'];
        this.adjustmentOrgList = this.beanExam.adjustmentOrgList;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}