  //CRM客户群查询
  custGroupQuery() {
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    this.custGroupQueryBean.userId = this.userId;
    this.custGroupQueryBean.sysCode = '122';
    this.custGroupQueryBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
    this.custGroupQueryBean.serverName = 'ywsn';
    this.custGroupQueryBean.validateType = '0';
    this.custGroupQueryBean.extraInfo = 'ywsn';
    this.custGroupQueryBean.encryptType = 'HS256';
    this.httpService.tokenApply(this.custGroupQueryBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let tokens = data.tokenCode;
        let url = TZB_HTTP_SX + 'ifs/visiturl.do?token_id=' + tokens + '&tranFlag=batGroup&user_id=' + this.userId + '&consumer_id=CRM&domain_name=ywsn&url=http://10.1.39.140:8011/searchPaymentTool&bankCode=BR00A&bankCode=BR00A';
        let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
        let closeOnLoseFocus = 'closeOnLoseFocus';
        this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
      }
    })
  }
