  selectCustGroup(index) {
    // 查询客户群
    this.selectCustGroupBean.groupStatus = '1';
    if (this.isN) {
      this.selectCustGroupBean.searchScope = index;
    }
    if (index == 3) {
      this.selectCustGroupBean.pageNum = this.pageNum1;
      this.selectCustGroupBean.pageSize = this.pageSize1;
    } else if (index == 4) {
      this.selectCustGroupBean.pageNum = this.pageNum2;
      this.selectCustGroupBean.pageSize = this.pageSize2;
    }
    // let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    // if(mesg.extParam.managePermission && mesg.extParam.managePermission == 'Y'){
    //   this.isN = false;
    //   this.selectCustGroupBean['searchScope'] = '2';
    // }else{
    //   this.selectCustGroupBean['ownerId'] = '';
    // }
    // this.selectCustGroupBean["ownerId"] = this.selectCustGroupBean["ownerId"];
    let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (mesg.extParam.managePermission && mesg.extParam.managePermission == 'Y') {
      this.isN = false;
      this.selectCustGroupBean['searchScope'] = '2';
      this.selectCustGroupBean['ownerId'] = '';
    } else {
      this.selectCustGroupBean['ownerId'] = mesg['userId'];//我的客户群总体信息查询，群主
    }
    this.selectCustGroupBean["partyClassificationTypeId"] = 0;
    this.httpService.selectCustGroup(this.selectCustGroupBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.selectCustGroupBean = data;
        this.selectCustGroupBean.groupList = data.groupList;
        let contact_arr = [];
        this.selectCustGroupBean.groupList.forEach((item, i) => {
          contact_arr = [];
          item.contactList.forEach(item_2 => {
            contact_arr.push(item_2.roleName);
            this.selectCustGroupBean.groupList[i]["contactListStr"] = contact_arr.join(';');
          });
        });
        if (index == 3) {
          this.groupList1 = this.selectCustGroupBean.groupList;
          this.groupList1.forEach((element, i) => {
            this.groupType.forEach(element2 => {
              if (element2.value == element.groupType) {
                this.groupList1[i].groupType = element2.label;
              }
            });
          });
          this.groupList1.forEach((element, i) => {
            let arr = [];
            if (element.contactList) {
              element.contactList.forEach(element2 => {
                arr.push(element2.roleName)
              });
            }
            let arr_str = arr.join(',')
            this.groupList1[i].contactList = arr_str;
          });
        } else if (index == 4) {
          this.groupList2 = this.selectCustGroupBean.groupList;
          this.totalNum2 = data.totalNum;
        }
        this.adminSelect();// 管理人员查询赋值
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
  adminSelect() {
    // 管理人员查询赋值
    this.selectCustGroupBean.groupList.forEach(item => {
      item.contactListArr = [];
      item.contactList.forEach(item1 => {
        let temp = [];
        temp = _.clone(item1.roleName);
        item.contactListArr.push(temp);
      })
    })
  }

  //代办及提醒
  pendingScheduleGroupQry() {
    let params = {
      statusId: null
    }
    let successFnc = data => {
      this.items = [];
      // 待办假数据
      if (data.scheduleList) {
        data.scheduleList.some(item => {
          if (this.items.length < 3) {
            this.items.push(item)
          }
        })
      }
    }
    this.commfunc.interfaceComFnc(this.httpService.pendingScheduleGroupQry(params), successFnc, this.msgs)
  }
