  //模板列表查询
  queryTargetCustomerExploreList(index) {
    this.loadingShow.flag = true;
    let params = {
      requestType: '3',
      queryScene: index,
    }
    this.httpService.queryTargetCustomerExploreList(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.loadingShow.flag = false;
        if (index == 0) {
          this.templeteList0 = data.screenTemplateList;
        } else {
          this.templeteListDialog = data.screenTemplateList;
        }
      }
      else {
        this.loadingShow.flag = false;
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.loadingShow.flag = false;
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
